<template>
	<div class="concact">
		<p class="concact-img" @click="showTencentFn"></p>
		<p class="concact-img" @click="showPhoneFn"></p>
		<div class="tencent common-cent" v-if="showTencent">
			<div class="qrcode"></div>
			<div class="account-number">
				<p>微信号：</p>
				<p>17051208610</p>
			</div>
		</div>
		<div class="phone common-cent" v-if="showPhone">
			张：17051208610
		</div>
	</div>
</template>

<script>
export default{
	name: 'Concact',
	data() {
		return {
			showTencent: false,
			showPhone: false
		}
	},
	methods:{
		showTencentFn(){
			this.showPhone = false
			this.showTencent = this.showTencent ? this.showTencent = false : this.showTencent = true;
			setTimeout(() => {
                this.showTencent = false
            }, 2000);
		},
		showPhoneFn(){
			this.showTencent = false
			this.showPhone = this.showPhone ? this.showPhone = false : this.showPhone = true;
			setTimeout(() => {
                this.showPhone = false
            }, 2000);
		}
	}
}
</script>

<style lang="stylus" scoped>
.concact{
	height: 25vh;
	width: 20vw;
	background-color: #ABDEFF;
	position: fixed;
	right: 2px;
	z-index: 10;
	top: 0;
	bottom: 20px;
	margin: auto;
	.concact-img{
		width: 100%;
		height: 8vh;
		overflow: hidden;
		&:nth-child(1){
			background: url('../../../static/img/tencent.png') 0 0 no-repeat;
			background-size: 100% 100%;
		}
		&:nth-child(2){
			background: url('../../../static/img/phone.png') 0 0 no-repeat;
			background-size: 100% 100%;
		}
	}
	.common-cent{
		width: 38vw;
		height: 8vh;
		background: rgba(0,0,0,0.3);
		border-radius: 5px;
		font-size: 2vh;
		padding: 5px;
		position: absolute;
		left: -38vw;
		white-space: nowrap;
		color: #ffffff;
	}
	.tencent{
		top: 0;
		display: flex;
		align-items: center;
		justify-content: space-around;
		.qrcode{
			width: 10vw;
			height: 6vh;
			background: url('../../../static/img/qrcode1.jpg') 0 0 no-repeat;
			background-size: 100% 100%;
		}
	}
	.phone{
		top: 8vh;
		line-height: 8vh;
		text-align: center;
	}
}
</style>


