<template>
	<div>
		<transition name="fade">
			<div class="menu" v-if="open" @click="closeMenu">
				<div class="bg-opacity"></div>
				<div class="wrap">
					<router-link to="/home" class="router-wrapper">
						<img src="../../../static/img/home.png" alt="" />
						<span>首页</span>
					</router-link>
					<router-link to="/product" class="router-wrapper">
						<img src="../../../static/img/product.png"/>
						<span>产品与服务</span>
					</router-link>
					<router-link to="/aboutUs" class="router-wrapper">
						<img src="../../../static/img/us.png"/>
						<span>关于我们</span>
					</router-link>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
export default{
	name: 'Menu',
	props:{
		open: false
	},
	data() {
		return {
			
		}
	},
	methods:{
		closeMenu() {
			this.$emit('closeMenu', false)
		}
	}
}
</script>

<style lang="stylus" scoped>
.menu{
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 50px;
	z-index: 100;
	.bg-opacity{
		width: 100%;
		height: 100%;
		background-color: #333333;
		opacity: 0.1;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -10;
	}
	.wrap{
		width: 180px;
		height: 100%;
		background:url('../../../static/img/footerBg.png') 0 0 no-repeat;
		background-size: 100% 100%;
		display: flex;
		flex-direction: column;
		font-size: 3vh;
		.router-wrapper{
			height: 10vh;
			line-height: 10vh;
			display: flex;
			justify-content: center;
			align-items: center;
			padding-left: 20px;
			color: #0078D7;
			span{
				flex: 1;
				margin-left: 10px;
			}
			img{
				width: 30px;
				height: 30px;
				vertical-align: middle;
			}
		}
	}
}
</style>


