<template>
  <div class="app">
  	<!-- 头部 -->
  	<div class="header">
  		<img @click="openMenu" src="../static/img/menu.png"/>
  		<router-link to="/home" class="title">PKC-H05</router-link>
  	</div>
  	<!-- 菜单 -->
  	<v-menu :open="open" @closeMenu="closeMenu"></v-menu>
  	<!-- 视图层 -->
		<keep-alive>
			<router-view></router-view>
		</keep-alive>
		<!-- 右侧固定栏 -->
		<!--<v-concact></v-concact>-->
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
  	return{
  		open: false
  	}
  },
  methods:{
  	openMenu(){
  		this.open = this.open ? this.open =false: this.open = true
  	},
  	closeMenu(msg) {
        this.open = msg
    }
  }
}
</script>

<style lang="stylus">
.app{
	position: relative;
	min-height: inherit;
	background: #FFFFFF;
	.header{
		height: 50px;
		width: 100 vw;
		z-index: 100;
		display: inline-flex;
		padding: 0 20px;
		align-items: center;
		position: fixed;
		top: 0;
		left: 0;
		background: url('../static/img/footerBg.png') 0 0 no-repeat;
		background-size: 100% 100%;
		color: #0078D7;
		font-weight: 700;
		font-family: 'Comic Sans MS';
		img{
			height: 38px;
			width: 50px;
			vertical-align: middle;
		}
		.title{
			display: block;
			width: 200px;
			font-size: 2.8vh;
			text-align: center;
			margin: auto;
			color: #0078D7;
		}
	}
}
</style>



