<template>
	<div class="aboutUs">
		<div class="wrap">
			<!-- title -->
			<div class="aboutUs-header">
				<p>关于我们</p>
				<p>About Us</p>
			</div>
			<!-- content -->
			<div class="content">
				<div class="wrapper">
					<div class="title">
						<p class="line"></p>
						<p class="text">关于我们</p>
						<p class="line"></p>
					</div>
					<div class="content">
						皮卡充科技有限公司，是一家致力于打造性价比高的线下媒介，建设“共建共享、全民健康”的新媒体平台企业。
					</div>
				</div>
				<div class="wrapper">
					<div class="title">
						<p class="line"></p>
						<p class="text">CEO寄语</p>
						<p class="line"></p>
					</div>
					<div class="content">
						我们努力为运营商提供性价比更高的产品，更专业的免费服务，帮助运营商更快、更好盈利。我们相信只有真正帮助运营商成功，我们才有机会成功，这也是我们全体皮卡充人共同坚守的理念和价值观。
					</div>
				</div>
				<div class="wrapper">
					<div class="title">
						<p class="line"></p>
						<p class="text">联系我们</p>
						<p class="line"></p>
					</div>
					<div class="content last-content">
						<div class="left">
							<p>
								<img src="../../static/img/company.png"/>
								<span>广州皮卡充科技有限公司</span>
							</p>
							<p>
								<img src="../../static/img/address.png"/>
								<span>广州市天河区燕岭路93号<br />新燕大厦1103</span>
							</p>
							<p>
								<img src="../../static/img/concact.png"/>
								<span>商务合作：张先生<br />17051208610(微信同号)</span>
							</p>
						</div>
						<div class="right">
							<img src="../../static/img/qrcode1.jpg" alt="" />
						</div>
					</div>
				</div>
			</div>
			<!-- map -->
			<div class="mapWrapper" id="pkcMap"></div>
		</div>
	</div>
</template>

<script>
import {MP} from '../../static/js/baiduMap'
export default {
  name: 'AboutUs',
  data () {
    return {}
  },
	mounted() {
		this.$nextTick(function () {
			MP("wZxI3Gm8rGDA3spxKB8S67Xq02UFdjm7").then( BMap => {
	            var map = new BMap.Map("pkcMap");            // 创建Map实例
	            var point = new BMap.Point(113.3349500567,23.1669301529); // 创建点坐标
	            map.centerAndZoom(point,16);
	            map.enableScrollWheelZoom();
				var sContent ="<div class='sContent'><h4 class='map-company' style='font-size:0.2rem;'>广州皮卡充科技有限公司</h4>" +
					"<p style='font-size:0.2rem;'>地址:&nbsp;广州市天河区燕岭路新燕大厦93号1103</p>"
				 var opts = {
  					width : 88,     // 信息窗口宽度
  					height: 88
				}
				var infoWindow = new BMap.InfoWindow(sContent,opts);
				var marker = new BMap.Marker(point);
				map.addOverlay(marker);
				marker.openInfoWindow(infoWindow);
				marker.addEventListener('click',function(){
					marker.openInfoWindow(infoWindow);
				})
			})
		})
	}
}
</script>

<style lang="stylus" scoped>
.aboutUs{
	width: 100vw;
	text-align: center;
	font-family: "微软雅黑";
	margin-top: 50px;
	font-size: 2vh;
	.wrap{
		width: 100%;
		.aboutUs-header{
			width: 100%;
			height: 180px;
			background: url('../../static/img/bg2.png') 0 0 no-repeat;
			background-size: 100% 100%;
			color: #FFFFFF;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			p{
				font-size: 4vh;
				&:nth-child(1){
					font-weight: 500;
					font-family: "华文细黑";
				}
				&:nth-child(2){
					font-size: 3vh;
					font-weight: 200;
					width: 250px;
					margin-top: 5px;
					font-family: "等线 (正文)";
					border-top: 1px solid #FFFFFF;
				}
			}
		}
		.content{
			margin-top: 5px;
			margin-bottom: 5px;
			font-family: "微软雅黑";
			font-size: 3vh;
			.wrapper{
				margin: 30px auto;
				.title{
					color: #30AEFF;
					display: flex;
					justify-content: center;
					align-items: center;
					margin: 10px auto;
					.text{
						margin: 0 10px;
					}
					.line{
						width: 35px;
						height: 2px;
						background-color: #30AEFF;
					}
				}
				.content{
					width: 85vw;
					margin: 0 auto;
					font-family: "宋体";
					font-size: 2.2vh;
					text-align: left;
					text-indent: 2em;
				}
				.last-content{
					display: flex;
					text-indent: 0;
					justify-content: center;
					align-items: center;
					font-size: 2.0vh;
					.left{
						flex: 3;
						display: flex;
						flex-direction: column;
						p{
							flex: 1;
							display: flex;
							align-items: center;
							margin-bottom: 3px;
							span{
								margin-left: 6px;
							}
							img{
								width: 12%;
								height: 12%;
								vertical-align: middle;
							}
						}
					}
					.right{
						flex: 1;
						img{
							width: 90%;
							height: 90%;
							vertical-align: middle;
						}
					}
				}
			}
		}
		.mapWrapper{
			width: 98vw;
			height: 40vh;
			margin: auto;
		}
	}
}
</style>


